<template>
  <div class="">
    <div class="liveNav">
      <div class="item active">精选</div>
      <div class="item">商业财经</div>
      <div class="item">男女交际</div>
      <div class="item">文化讲堂</div>
      <div class="item">艺术海洋</div>
      <div class="item">技能掌握</div>
      <div class="item">法律课程</div>
      <div class="item">养生观念</div>
      <div class="item">畅游国际</div>

    </div>
    <el-carousel :interval="5000" arrow="always" height="200px">
      <el-carousel-item v-for="item in 4" :key="item">
      </el-carousel-item>
    </el-carousel>

    <div class="main1 main2  bgfff" style=" margin-top: 5px;"  v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
      <div class="title" style="margin-left: 0;padding-top: 5px;border-bottom: 1px solid #f2f2f2;margin-bottom: 5px">
        <span class="txt"><span style="height: 100%;background: RGBA(0, 208, 0, 1);color: RGBA(0, 208, 0, 1);margin-right: 5px;">1</span>正在直播</span>
      </div>
      <div class="list" style="width: 100%;overflow: hidden;">
        <el-row :gutter="2" style="margin: 5px;">
          <el-col :span="12" v-for="(item,index) in list" :key="index">
              <div class="item " style="text-align: left;box-shadow:0 0 8px rgba(51,51,51,0.33);border-radius: 10px;margin: 5px;padding-bottom: 10px;">
                <div class="imgs">
                  <img src="../../../static/img/home/Advertisement.png" style="width: 100%;height: auto; ">
                </div>
                <p style="font-size: 16px;">王洪涛-实战路演 </p>
                <span class="tips" style="margin-left: 5px;display: inline-block">商业财经</span>
                <div style="margin: 10px;"></div>
                <div style="margin-left: 10px;"><span style="font-size: 12px;color:#AAAAAA;">2018-09-19  19:00:00</span></div>
                <div class="text-tc" style="margin: 10px;">
                  <el-button type="primary" round style="padding: 5px 10px"><i class="el-icon-time" style="margin-right: 5px"></i>去预约</el-button>
                </div>
              </div>

          </el-col>
        </el-row>
      </div>
    </div>


    <div class="pageLoad" v-if="pageLoad">
      <i class="el-icon-loading"></i>
      上划自动加载
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        list:[1,2,3,4],
        pageLoad:false
      }
    },
    methods:{
      loadMore() {
        this.pageLoad = true;
        setTimeout(() => {
          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 2; i++) {
            this.list.push(last + i);
          }
          this.pageLoad = false;
        }, 2500);
      }
    },
    mounted(){

    }
  }
</script>


<style scoped>
  .tips{
    color: RGBA(243, 152, 0, 1);
    font-size: 12px;
    border: 1px solid RGBA(243, 152, 0, 1);
    padding: 2px 5px;
    border-radius: 10px;
  }
  .main2 .item .imgs:before{
    display: none;
  }
</style>



